exports.rawCodeExample="import React from 'react';\n\nconst Contact = () => {\n  return (\n    <div className='standard-content'>\n      <div className='text-body'>\n        <h1 className='header-title'>Contact</h1>\n        <p style={{textAlign: 'center'}}>\n          You can contact me at: <a style={{marginLeft: '10px'}} href='mailto:ryanpaixao@gmail.com'>ryanpaixao@gmail.com</a>\n        </p>\n      </div>\n    </div>\n  );\n}\n\nexport default Contact;\n";

